.accordion-underline {
  width: 80%;
  height: 1px;
  background-color: #ccc;
  position: absolute;
  bottom: 0;
}
.accordion-item-container {
  font-size: 18px;
  /* border-bottom: 1px solid #ccc; */
  width: 50%;
  cursor: pointer;
  position: relative;
  width: 80%;
}
.accordion-question {
  cursor: pointer;
  display: inline-block;
  font-size: 16px;
  color: #27272a;
  font-weight: 400;
  margin-bottom: 23px;
  margin-top: 19px;
}

.accordion-answer {
  font-size: 15px;
  color: #524f4f;
  margin: 0;
  pointer-events: none;
  -webkit-animation: showAnswer 0.5s ease forwards alternate;
          animation: showAnswer 0.5s ease forwards alternate;
}
/* .accordion-answer.open {
  animation: hideAnswer 0.5s ease forwards alternate;
} */

/* @keyframes hideAnswer {
  from {
    padding-top: 5px;
    padding-bottom: 25px;
  }

  to {
    padding: 0;
  }
} */
@-webkit-keyframes showAnswer {
  to {
    padding-top: 5px;
    padding-bottom: 25px;
  }
}
@keyframes showAnswer {
  to {
    padding-top: 5px;
    padding-bottom: 25px;
  }
}

.accordion-question.bold {
  font-weight: bold;
  color: rgb(68, 64, 64);
}

.arrow {
  display: inline-block;
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-80%) scale(1.1);
          transform: translateY(-80%) scale(1.1);
  transition: 0.3s;
}

.arrow.down {
  -webkit-transform: rotate(-180deg) scale(1.1) translateY(6px);
          transform: rotate(-180deg) scale(1.1) translateY(6px);
}

.accordion-question:hover {
  color: #f47c57;
}

.card-title {
  font-size: 36px;
  font-weight: bold;
  /* font-family: sans-serif; */
}

.card-container {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 60vw;
  height: 550px;
  margin: 0 auto;
  background-color: rgb(253, 251, 251);
  border-radius: 15px;
  box-shadow: 0 0 50px rgb(0 0 0 / 25%);
}

.woman-image-block {
  height: 100%;
  position: relative;
  overflow: hidden;
}

.card-images {
  position: relative;
  width: 50%;
  height: 100%;
}

.faq-container {
  width: 50%;
  height: 100%;
  -webkit-transform: translateY(8%);
          transform: translateY(8%);
}

.woman-image {
  position: relative;
  left: -80px;
  z-index: 10;
  -webkit-transform: translateY(27%);
          transform: translateY(27%);
}

.background-woman-image {
  position: absolute;
  left: -113%;
  top: -48%;
}

.box-image {
  position: absolute;
  left: -10%;
  bottom: 25%;
  z-index: 15;
  -webkit-transform: translate(0, 10px);
          transform: translate(0, 10px);
  animation: levitate 4s linear infinite;
  -webkit-animation: levitate 5s linear infinite;
}
@-webkit-keyframes levitate {
  50% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}
@keyframes levitate {
  50% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

